orm: translateY(-100%);\n    transition: transform 0.4s ease;\n  }\n}\n\n.nav_list {\n  text-align: center;\n  display: flex;\n  flex-direction: column;\n  row-gap: 2.5rem;\n}\n\n.nav_link {\n  position: relative;\n  color: white;\n  font-weight: 400;\n}\n\n.nav_link::after {\n  content: \"\";\n  width: 0;\n  height: 2px;\n  background-color: #fff;\n  position: absolute;\n  left: 0;\n  bottom: -0.5rem;\n  transition: width 0.3s;\n}\n\n.nav_close {\n  position: absolute;\n  top: 1rem;\n  right: 1.5rem;\n}\n\n/* Show menu  */\n.show-menu {\n  top: 0;\n}\n\n/* Blur  */\n.blur-header::after {\n  content: \"\";\n  position: absolute;\n  width: 1000%;\n  height: 100%;\n  background-color: hsla(0, 0%, 0%, 0.3);\n  backdrop-filter: blur(24px);\n  -webkit-backdrop-filter: blur(24px);\n  top: 0;\n  left: 0;\n  z-index: -1;\n}\n\n/* Search */\n.search-box {\n  width: 320px;\n  background-color: #fff;\n  margin: 30px auto;\n  border-radius: 5px;\n  align-items: center;\n}\n\n.row {\n  display: flex;\n  align-items: center;\n  padding: 4px 20px;\n}\n\ninput {\n  flex: 1;\n  height: 50px;\n  background: transparent;\n  border: 0;\n  outline: 0;\n  font-size: 18px;\n  color: #333;\n}\n\ninput:focus,\ntextarea:focus {\n  outline: 4px solid hsl(38, 61%, 73%);\n}\n\na,\nbutton,\ninput,\ntextarea {\n  min-width: 44px;\n  min-height: 44px;\n  padding: 12px;\n}\n\n/* button .search-outline {\n  width: 44px;\n  font-size: 22px;\n} */\n\n/* Result Box */\n.result-box {\n  max-height: 200px;\n  overflow-y: scroll;\n}\n\n.result-box ul {\n  border-top: 1px solid #999;\n  padding: 15px 10px;\n}\n\n.result-box ul li {\n  list-style: none;\n  border-radius: 3px;\n  padding: 15px 10px;\n  text-align: left;\n  cursor: pointer;\n}\n\n.result-box ul li:hover {\n  background: hsl(38, 61%, 73%);\n}\n\n/* List Restaurants */\n.restaurants {\n  padding: 34px;\n}\n.heading {\n  max-width: 968px;\n  margin-left: auto;\n  margin-right: auto;\n  font-size: 1.5rem;\n  font-weight: 500;\n  color: #fff;\n}\n\n.listRestaurants {\n  max-width: 1200px;\n  margin-right: auto;\n  margin-left: auto;\n  display: grid;\n  grid-template-columns: repeat(1, 1fr);\n  gap: 1rem;\n  margin-top: 1.5rem;\n}\n\n.box .box-img {\n  width: 100%;\n  height: 270px;\n  cursor: pointer;\n}\n.box .box-img img {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  border: 4px solid hsl(38, 61%, 73%);\n  border-radius: 4px;\n}\n.box .box-img img:hover {\n  transform: translateY(-10px);\n  transition: 0.2s all linear;\n}\n.box h3 {\n  font-size: 0.9rem;\n  font-weight: bold;\n  color: #fff;\n}\n.box p {\n  font-size: 14px;\n  color: #fff;\n}\n.box span {\n  font-size: 14px;\n  color: #fff;\n}\n\n/* NEW */\n.text-center {\n  text-align: center;\n}\n\n.section {\n  position: relative;\n  padding-block: 70px;\n  overflow: hidden;\n  z-index: 1;\n}\n\n.container {\n  max-width: 1120px;\n  margin-inline: 1.5rem;\n}\n\n.section-subtitle {\n  position: relative;\n  color: hsl(38, 61%, 73%);\n  font-weight: 700;\n  text-transform: uppercase;\n  letter-spacing: 0.4em;\n  margin-block-end: 12px;\n}\n\n.label-2 {\n  font-size: 0.8rem;\n}\n\n.title-2 {\n  font-size: 1.2rem;\n}\n\n.text-white {\n  color: #fff;\n}\n\n.headline-1 {\n  font-size: 2rem;\n  color: #fff;\n}\n\n.headline-2 {\n  font-size: 1.5rem;\n  color: #fff;\n}\n\n.restaurant-item {\n  display: grid;\n  gap: 40px;\n}\n\n.list .section-title {\n  margin-block-end: 40px;\n}\n\n.list-card {\n  position: relative;\n  overflow: hidden;\n  border-radius: 5px;\n  cursor: pointer;\n}\n\n.list-card .card-content {\n  background-image: linear-gradient(\n    to top,\n    hsla(0, 0%, 0%, 0.9),\n    hsla(0, 0%, 0%, 0.7),\n    transparent\n  );\n  position: absolute;\n  bottom: 0;\n  width: 100%;\n  padding: 35px 35px 25px;\n}\n\n.list-card .rating {\n  position: absolute;\n  top: 30px;\n  left: 25px;\n  padding: 5px 10px;\n  color: hsl(38, 61%, 73%);\n  background-color: hsla(0, 0%, 0%, 1);\n  font-weight: 700;\n  letter-spacing: 0.15em;\n  line-height: 14px;\n  font-size: 16px;\n}\n\n.list-card .rating .star {\n  font-size: 16px;\n}\n\n.list-card .card-subtitle {\n  color: hsl(38, 61%, 73%);\n  text-transform: uppercase;\n  font-weight: 600;\n  letter-spacing: 0.4em;\n  margin-block-end: 5px;\n}\n\n.list-card .card-title {\n  color: #fff;\n  margin-block-end: 5px;\n}\n\n.list-card .card-banner .img-cover {\n  transition: 500ms ease;\n}\n\n.list-card:is(:hover, :focus-within) .img-cover {\n  transform: scale(1.05);\n}\n\n.img-holder {\n  aspect-ratio: 350 / 450;\n  overflow: hidden;\n  background-color: hsla(0, 0%, 13%, 1);\n}\n\n.img-cover {\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n}\n\n/* Active Link */\n/* .active-link::after {\n  width: 70%;\n} */\n\n/* Back to Top */\n.back-top-btn {\n  position: fixed;\n  bottom: 20px;\n  right: 20px;\n  background-color: hsl(38, 61%, 73%);\n  color: hsla(40, 12%, 5%, 1);\n  font-size: 22px;\n  width: 50px;\n  height: 50px;\n  border-radius: 50%;\n  display: grid;\n  place-items: center;\n  box-shadow: 0px 0px 25px 0px hsla(0, 0%, 0%, 0.25);\n  transition: 250ms ease;\n  opacity: 0;\n  visibility: hidden;\n  z-index: 4;\n}\n\n.back-top-btn:is(:hover, :focus-visible) {\n  background-color: #fff;\n  color: hsl(38, 61%, 73%);\n}\n\n.back-top-btn.active {\n  opacity: 1;\n  visibility: visible;\n}\n\n.skip-link {\n  position: absolute;\n  top: 0;\n  left: 0;\n  background-color: hsl(0, 0%, 23%);\n  color: white;\n  padding: 8px;\n  z-index: 100;\n  cursor: pointer;\n  transform: translateY(-100%);\n  transition: transform 0.3s ease;\n}\n\n.skip-link:focus {\n  transform: translateY(0);\n}\n\n/* Scroll Bar */\n::-webkit-scrollbar {\n  width: 0.6rem;\n  border-radius: 0.5rem;\n  background-color: hsl(0, 0%, 10%);\n}\n\n::-webkit-scrollbar-thumb {\n  border-radius: 0.5rem;\n  background-color: hsl(0, 0%, 20%);\n}\n\n::-webkit-scrollbar-thumb:hover {\n  background-color: hsl(0, 0%, 30%);\n}\n\n/* Hover Shine */\n.hover\\:shine {\n  position: relative;\n}\n\n.hover\\:shine::after {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 50%;\n  height: 100%;\n  background-image: linear-gradient(to right, transparent 0%, #fff6 100%);\n  transform: skewX(-0.08turn) translateX(-180%);\n}\n\n.hover\\:shine:is(:hover, :focus-within)::after {\n  transform: skewX(-0.08turn) translateX(275%);\n  transition: 1000ms ease;\n}\n\n.has-before {\n  position: relative;\n  z-index: 1;\n}\n\n.has-before::before {\n  content: \"\";\n  position: absolute;\n}\n\n/* Modal */\n.modal {\n  display: none;\n  position: fixed;\n  z-index: 1000;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  overflow: auto;\n  background-color: rgba(0, 0, 0, 0.5);\n}\n\n.modal-content {\n  background-color: #fefefe;\n  margin: 20% auto;\n  padding: 20px;\n  border: 1px solid #888;\n  width: 20%;\n  text-align: center;\n  border-radius: 6px;\n}\n\n.loader {\n  width: 30px;\n  height: 30px;\n  border: 2px solid #2c3e50;\n  border-radius: 50%;\n  border-top-color: #0001;\n  display: inline-block;\n  animation: loadingspinner 0.7s linear infinite;\n}\n\n.mb-2 {\n  margin-bottom: 1rem;\n}\n\n@keyframes loadingspinner {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n.close {\n  color: #aaa;\n  float: right;\n  font-size: 28px;\n  font-weight: bold;\n}\n\n.close:hover,\n.close:focus {\n  color: black;\n  text-decoration: none;\n  cursor: pointer;\n}\n\n/* Detail Resto */\n.result-grid {\n  padding: 0 2rem;\n  margin-block-start: 20px;\n  margin-block-end: 20px;\n}\n\n.result-container {\n  padding: 1rem 0;\n}\n\n.detail-poster img {\n  display: block;\n  border: 4px solid #e4c590;\n  width: 100%;\n  justify-content: center;\n  align-items: center;\n  background-position: center;\n  border-radius: 6px;\n}\n.detail-info {\n  text-align: center;\n  color: #fff;\n  padding-top: 3rem;\n}\n\n.detail-info .city {\n  margin: 1rem 0;\n}\n\n.detail-info .address {\n  margin: 1rem 0;\n}\n\n.detail-info .rating {\n  background: #e4c590;\n  display: inline-block;\n  padding: 0.5rem;\n  border-radius: 3px;\n}\n.detail-info .description {\n  max-width: 800px;\n  margin: 1rem auto;\n}\n\n.detail-info .menu {\n  max-width: 800px;\n  margin: 1rem auto;\n}\n\n.detail-info .awards {\n  font-weight: 300;\n  font-size: 0.9rem;\n}\n.detail-info .awards i {\n  background: #e4c590;\n  margin: 1rem 0.7rem 0 0;\n}\n\n/* Dot */\n.dot {\n  display: inline-block;\n  width: 6px;\n  height: 6px;\n  margin-right: 6px;\n  border-radius: 50%;\n  background-color: #e4c590;\n}\n\n/* Review */\n.review-container {\n  display: grid;\n  grid-template-columns: repeat(minmax(300px, 3fr));\n  gap: 0.5rem;\n}\n\n.review-card {\n  background: white;\n  color: black;\n  margin-block-start: 30px;\n  padding: 1rem 2rem;\n  border-radius: 0.8rem;\n  box-shadow: 5px 5px 20px #6d6b6b6b;\n  color: white;\n}\n\n.userdetails {\n  display: flex;\n}\n\n.detbox {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n}\n\n.detbox p {\n  margin: 0;\n}\n\n.detbox .name {\n  color: hsl(0, 0%, 81%);\n  font-size: 0.9rem;\n  margin-bottom: 0.1rem;\n  font-weight: 600;\n}\n\n.detbox .designation {\n  color: #49505a;\n  opacity: 50%;\n  font-size: 0.8rem;\n}\n\n.detbox .name.dark {\n  color: #49505a;\n}\n\n.review h4 {\n  font-size: 1.4rem;\n  color: #f3deff;\n  font-weight: 600;\n  line-height: 1.5;\n  margin-bottom: 0.8rem;\n}\n\n.review.dark h4 {\n  color: #4b5258;\n}\n\n.review p {\n  font-size: 0.95rem;\n  color: #f3deff;\n  font-weight: 500;\n  opacity: 50%;\n  line-height: 1.5;\n}\n\n.review.dark p {\n  color: #0e0e0e;\n}\n\na {\n  text-decoration: none;\n}\n\n/* Like */\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #db0000;\n\n  position: fixed;\n  bottom: 32px;\n  right: 32px;\n  border-radius: 50%;\n  border: 0;\n\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n/* add Review */\n.reviewContainer {\